import { Link } from 'react-router-dom';
import AuthForm from './AuthForm';
import Header from './Header.js';

const Register = ({ handleSubmit, handleChange, formValue }) => {

  return (
    <>
      <Header enter={'/sign-in'} enterTitle={""} />
      <AuthForm handleSubmit={handleSubmit} handleChange={handleChange} formValue={formValue} title={""} buttonTitle={""} />
      <p className='autorization__question' > ?<Link to={'/sign-in'} className='autorization__question'></Link></p>
    </>);
}

export default Register;